import { 
  CDropdown, CDropdownMenu, CDropdownItem, 
  CDropdownToggle
} from "@coreui/react";

const ActionButton = (props) => {
  const { items } = props;
  return (
    <CDropdown>
      <CDropdownToggle size="sm" color="warning">
        Action
      </CDropdownToggle>
      <CDropdownMenu>
        {items.map((item, i) => (
          <CDropdownItem key={i} onClick={item.onClick}>{item.text}</CDropdownItem>
        ))}
      </CDropdownMenu>
    </CDropdown>
  )
}

export default ActionButton
