import { CModal, CModalHeader, CModalBody, CModalFooter, CModalTitle } from "@coreui/react";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { changePassword, createUser, updateUser } from "../../api";
import RenderForm from "../../components/RenderForm";
import LoadingButton from "../../components/LoadingButton";
import Role from "../../constants/roles";

const Modal = (props) => {
  const { modalTitle, show, onCancel, afterSubmit, type, data } = props;
  const [name, setName] = useState(data?.name ?? "");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("")
  const [position, setPosition] = useState("");
  const [level, setLevel] = useState("");
  const [loadingBtn, setLoadingBtn] = useState(false);

  const levels = Object.keys(Role).map((r, i) => ({value: i + 1, label: r}))

  const fields = [
    [
      {label: "Name", value: name, onChange: e => setName(e.target.value), type: "text"},
      {label: "Email", value: email, onChange: e => setEmail(e.target.value), type: "email"},
      {label: "Position", value: position, onChange: e => setPosition(e.target.value), type: "text"},
      {label: "Level", value: level, onChange: e => setLevel(e), options: levels, type: "select"},
      {label: "Password", value: password, onChange: e => setPassword(e.target.value), type: "password"},
      {label: "Confirm Password", value: confirmPassword, onChange: e => setConfirmPassword(e.target.value), type: "password"},
    ]
  ]

  const getFields = () => {
    if(type === "create") return fields;
    else if(type === "edit") {
      let f = fields[0].filter(f => f.type !== "password");
      return [f];
    } else {
      return [[
        {label: "New Password", value: password, onChange: e => setPassword(e.target.value), type: "password"},
        {label: "Confirm New Password", value: confirmPassword, onChange: e => setConfirmPassword(e.target.value), type: "password"},
      ]]
    }
  }

  const clearForm = () => {
    setName("")
    setEmail("")
    setPosition("")
    setLevel("")
    setPassword("")
    setConfirmPassword("")
  }

  const onSubmit = async () => {
    setLoadingBtn(true);
    try {
      if(type === "create") {
        if(password === "") return toast.error("Password tidak boleh kosong!");
        if(password.length < 6) return toast.error("Password minimal 6 karakter!");
        if(password !== confirmPassword) return toast.error("Konfirmasi password tidak sesuai!");
        await createUser({name, email, position, level: level.value, password});
      } else if(type === "edit") {
        await updateUser({name, email, position, level: level.value, id: data.id});
      } else {
        if(password === "") return toast.error("Password tidak boleh kosong!");
        if(password.length < 6) return toast.error("Password minimal 6 karakter!");
        if(password !== confirmPassword) return toast.error("Konfirmasi password tidak sesuai!");
        await changePassword({newPassword: password}, data.id);
      }
      clearForm();
      onCancel();
      await afterSubmit();
    } catch (err) {}
    finally {
      setLoadingBtn(false);
    }
  }

  useEffect(() => {
    const levels = Object.keys(Role).map((r, i) => ({value: i + 1, label: r}))
    if(type === "edit") {
      setName(data?.name ?? "");
      setEmail(data?.email ?? "");
      setLevel(levels.find(l => l.value === data?.level));
      setPosition(data?.position ?? "");
    }
  }, [data?.name, data?.email, data?.level, data?.position, type]);

  return (
    <CModal show={show} size="lg" onClose={onCancel}>
      <CModalHeader>
        <CModalTitle>{modalTitle}</CModalTitle>
      </CModalHeader>
      <CModalBody>
        <RenderForm
          fields={getFields()}
          col={12}
        />
      </CModalBody>
      <CModalFooter>
        <button className="btn btn-secondary" onClick={() => {onCancel(); clearForm()} }>Cancel</button>
        {loadingBtn ? (<LoadingButton />) : (
          <button className="btn btn-primary" onClick={onSubmit}>Save</button>
        )}
      </CModalFooter>
    </CModal>
  )
}

export default Modal
