import React from "react";
import FormInput from "./FormInput";

const RenderForm = (props) => {
  const { fields, col } = props;
  return (
    <div className="row">
      {fields.map((field, i) => (
        <div className={`col-md-${col ?? 6}`} key={i}>
          {field.map((f, j) => (
            React.isValidElement(f) ? f :
            <FormInput 
              type={f.type}
              value={f.value}
              onChange={f.onChange}
              label={f.label}
              key={j}
              options={f?.options}
            />
          ))}
        </div>
      ))}
    </div>
  )
}

export default RenderForm
