import { 
  CModal, CModalHeader, CModalBody,
  CModalFooter, CModalTitle
} from "@coreui/react";
import LoadingButton from "./LoadingButton";

const ConfirmDialog = (props) => {
  const { show, onPositive, onNegative, text, btnLoading } = props;
  return (
    <CModal show={show} onClose={onNegative}>
        <CModalHeader>
          <CModalTitle>Konfirmasi!</CModalTitle>
        </CModalHeader>
        <CModalBody>
          {text ?? "Apakah anda yakin akan menghapus data?"}
        </CModalBody>
        <CModalFooter>
          <button className="btn btn-secondary" onClick={onNegative}>Tidak</button>
          {btnLoading ? (
            <LoadingButton color="danger" />
          ) : (
            <button className="btn btn-danger" onClick={onPositive}>Ya</button>
          )}
        </CModalFooter>
      </CModal>
  )
}

export default ConfirmDialog
