import { useState } from "react";
import { FaSearch } from "react-icons/fa";
import { CPagination } from "@coreui/react";

const DataTable = (props) => {
  const { columns, rows } = props?.data;
  const { onSearch, search: isSearch, pages, currentPage, onActivePageChange } = props;
  const [keyword, setKeyword] = useState("");

  const search = (e) => {
    e.preventDefault();
    onSearch(keyword);
  }
  
  if(props.data === undefined) return <div></div>
  return (
    <>
    {isSearch && 
      <div className="clear-fix">
        <form onSubmit={search}>
          <div className="float-right">
            <div className="d-flex">
              <div className="form-group mr-1">
                <input 
                  type="search" 
                  className="form-control" 
                  value={keyword} 
                  onChange={(e) => setKeyword(e.target.value)} 
                  placeholder="Search..." 
                />
              </div>
              <div className="form-group">
                <button className="btn btn-secondary" onClick={search}><FaSearch /></button>
              </div>
            </div>
          </div>
        </form>
      </div>
    }
    <table className="table">
      <thead>
        <tr>
          {columns?.map((r, i) => (
            <th key={i} style={r?.style ?? {}}>{r?.text ?? r}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {rows?.length === 0 ? (<tr>
          <td><h4>Data tidak ada!</h4></td>
        </tr>) : rows?.map((col, i) => (
          <tr key={i}>
            {col.map((c, j) => (
              <td style={c?.style ?? {}} key={j}>{c?.text ?? c}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
    {pages > 1 && (
      <CPagination
        activePage={currentPage}
        pages={props.pages || 1}
        onActivePageChange={(n) => onActivePageChange(n)}
      />
    )}
    </>
  )
}

export default DataTable
