import { CFormGroup } from "@coreui/react";
import Select from "react-select";

const FormInput = (props) => {
  const { label, type, value, onChange, options } = props;
  if (type === "textarea") return (
    <CFormGroup>
      <label>{label}</label>
      <textarea 
        onChange={onChange}
        className="form-control"
        rows="5"
        defaultValue={value}
      >
      </textarea>
    </CFormGroup>
  )
  else if(type === "select") {
    return <CFormGroup>
      <label>{label}</label>
      <Select 
        options={options}
        value={value}
        onChange={onChange}
      />
    </CFormGroup>
  }
  else return (
    <CFormGroup>
      <label>{label}</label>
      <input 
        type={type} 
        className="form-control" 
        value={value}
        onChange={onChange}
      />
    </CFormGroup>
  )
}

export default FormInput
