import axiosInstance from "./axiosInstance";

export async function deleteFile(id) {
  return await axiosInstance.delete(`/file/${id}`);
}

export async function uploadImage(img, ctId) {
  const formData = new FormData();
  formData.append("file", img);
  return await axiosInstance.put(`/image/upload/${ctId}`, formData);
}

export async function uploadFile(file, id) {
  const formData = new FormData();
  formData.append("file", file);
  return await axiosInstance.put(`/file/upload/${id}`, formData);
}
