import { CCard, CCardBody, CCardHeader } from "@coreui/react";
import { useEffect, useState } from "react";
import { deleteUser, getUser } from "../../api";
import Header from "../../components/Header";
import DataTable from "../../components/DataTable";
import LoadingState from "../../components/LoadingState";
import Role from "../../constants/roles";
import ActionButton from "../../components/ActionButton";
import Modal from "./Modal";
import ConfirmDialog from "../../components/ConfirmDialog";

const User = (props) => {
  const [data, setData] = useState({});
  const [modal, setModal] = useState(null);
  const [confirmDialog, setConfirmDialog] = useState(false);
  const [toDelete, setToDelete] = useState(null);
  const [toEdit, setToEdit] = useState(null);
  const [loadingState, setLoadingState] = useState(true);
  const [keyword, setKeyword] = useState("");
  const [totalPages, setTotalPages] = useState(0);
  const [currentPage, setCurrentPage] = useState(0);

  const loadData = async (search = null, page = 0) => {
    setLoadingState(true);
    try {
      const { data } = await getUser(search, page);
      formatData(data.content);
      setTotalPages(data.totalPages)
      setCurrentPage(data.number + 1);
    } catch (err) {}
    setLoadingState(false);
  }

  const formatData = (contents) => {
    const columns = ["Name", "Email", "Position", "Level", ""];
    const rows = contents.map(c => [c.name, c.email, c.position, showLevel(c.level), actionBtn(c)]);
    setData({columns, rows});
  }

  const showLevel = (level) => {
    return <span className="badge badge-light">{Object.keys(Role)[parseInt(level) - 1]}</span>
  }

  const actionBtn = (d) => {
    const items = [
      {text: "Edit", onClick: () => openModalEdit(d)},
      {text: "Change Password", onClick: () => openModalChangePassword(d)},
      {text: "Delete", onClick: () => openConfirmDialog(d)},
    ]
    return <ActionButton items={items} />
  }

  const openConfirmDialog = (d) => {
    setConfirmDialog(true);
    setToDelete(d);
  }

  const openModalChangePassword = (d) => {
    setModal("changePassword");
    setToEdit(d);
  }

  const openModalEdit = (d) => {
    setModal("edit");
    setToEdit(d);
  }

  const handleDelete = async () => {
    try {
      await deleteUser(toDelete.id);
      await loadData();
      setConfirmDialog(null);
    } catch (err) {}
  }

  const onSearch = (keyword) => {
    setKeyword(keyword);
    loadData(keyword, 0);
  }

  useEffect(() => {
    loadData();
  }, [])

  if(loadingState) return <LoadingState />
  return (
    <>
      <CCard>
        <CCardHeader>
          <Header 
            title="User"
            buttons={[{onClick: () => setModal("create"), text: "Tambah"}]}
          />
        </CCardHeader>
        <CCardBody>
          <DataTable 
            data={data}
            search={true}
            onSearch={onSearch}
            pages={totalPages}
            currentPage={currentPage}
            onActivePageChange={(n) => loadData(keyword, n)}
          />
        </CCardBody>
      </CCard>
      <Modal 
        modalTitle="Add User"
        show={modal === "create"}
        onCancel={() => setModal(null)}
        afterSubmit={async () => await loadData()}
        type="create"
      />
      <Modal 
        modalTitle="Edit User"
        show={modal === "edit"}
        onCancel={() => setModal(null)}
        afterSubmit={async () => await loadData()}
        type="edit"
        data={toEdit}
      />
      <Modal 
        modalTitle="Change Password"
        show={modal === "changePassword"}
        onCancel={() => setModal(null)}
        afterSubmit={async () => await loadData()}
        type="changePassword"
        data={toEdit}
      />
      <ConfirmDialog 
        show={confirmDialog}
        onNegative={() => setConfirmDialog(null)}
        onPositive={handleDelete}
      />
    </>
  )
}

export default User
