import axiosInstance from "./axiosInstance";

export async function getUser(search = null, page = 0) {
  let qs = `?size=10&page=${page > 0 ? page - 1 : page}`;
  if (search) qs += `&search=${search}`;
  return await axiosInstance.get(`/user${qs}`);
}

export async function getUserByLevel(level) {
  return await axiosInstance.get(`/user/level/${level}`);
}

export async function createUser(data) {
  return await axiosInstance.post("/user", data);
}

export async function deleteUser(id) {
  return await axiosInstance.delete(`/user/${id}`);
}

export async function updateUser(data) {
  return await axiosInstance.put("/user", data);
}

export async function changePassword(data, id) {
  return await axiosInstance.put(`/user/change-password/${id}`, data);
}

export async function changeMyPassword(data) {
  return await axiosInstance.put("/user/change-my-password", data);
}
